
import Scroller from 'vux/src/components/scroller/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import { mapGetters } from 'vuex'
export default {
    components: {
        Scroller,
        dateFormat
    },
    filters: {
        filterDate: function (starttime, endtime) {
            let now = Date.parse(new Date()) / 1000;
            if (now < starttime) {
                return "未开始"
            } else if (endtime <= now) {
                return "已结束"
            } else if (starttime <= now < endtime) {
                return "进行中"
            }
        },
        filterTime: function (starttime, endtime) {
            let startFormat = starttime * 1000;
            let endFormat = endtime * 1000;
            var start = dateFormat(startFormat, 'MM月DD');
            var end = dateFormat(endFormat, 'MM月DD');
            return `${start}至${end}`;
        },
        filterTime_p: function (starttime, endtime) {
            let startFormat = starttime * 1000;
            let endFormat = endtime * 1000;
            var start = dateFormat(startFormat, 'MM.DD HH:mm');
            var end = dateFormat(endFormat, 'MM.DD HH:mm');
            return `${start}-${end}`;
        },
        filterType: function (type) {
            switch (parseInt(type)) {
                case 1: return "徒步"; break;
                case 2: return "跑步"; break;
                case 3: return "骑行"; break;
                case 4: return "其他"; break;
            }
        }
    },
    methods: {

    },
    created() {
        this.$store.dispatch('get_activity_list', {begin: 0,offset: 100,uid: localStorage.getItem('loginopenid')})
        this.$store.dispatch('get_activity_official_list', {begin: 0,offset: 100,uid: localStorage.getItem('loginopenid')})
    },
    computed: {
        nowDate() {
            return Date.parse(new Date()) / 1000
        },
        calWidth() {
            return this.$store.state.activity.activity_official_list.length * 15.6 + .5;
        },
        ...mapGetters([
            'activity_list',
            'activity_official_list'
        ])
    }
}




